.listItem {
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  letter-spacing: 0.03em;
}
.listItem:not(:last-child) {
  margin-bottom: 8px;
}

.text {
  margin-right: 10px;
}

.btn {
  padding: 2px 4px;
  border: 1px solid #615e5e;
  border-radius: 4px;
  cursor: pointer;
  font-weight: 500;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 0.03em;
}
.btn:hover {
  background-color: #eb1212;
}
