.form {
  padding: 14px;
  font-weight: 500;
  font-size: 18px;
  line-height: 20px;
  letter-spacing: 0.03em;
  border: 1px solid #2c2a2a;
}

.label {
  display: flex;
  flex-direction: column;
}

.input {
  margin-bottom: 24px;
  padding: 4px 0px 4px 10px;
  /* width: 80%; */
  border: 1px solid #89888ba6;
  outline: none;
}
.input:focus {
  border: 1px solid #2f8cb8b2;
  box-shadow: 0px 0px 4px 2px #2f8cb8b2;
}

.labelName {
  margin-bottom: 8px;
}
